<template>
    <div id="forDirective">

        <h2>Mis conocimientos en programación son:</h2>
        <ul>
            <li v-for="habilidad in skills" :key="habilidad"> {{ habilidad }}</li>
        </ul>
        <hr>
        <input type="text" v-model="skill">
        <button v-on:click="agregarSkill">Agregar skill</button>
    </div>
</template>

<script>
export default {

    name: 'for-directive',
    data: function(){
        return{
            skills: ['html', 'css', 'js'],
            skill: '',
        }
    },
    methods:{

        agregarSkill: function(){
            this.skills.push(this.skill);
            this.skill = '';
        }
    },
}

</script>

<style>

#forDirective{
color: darkseagreen;
padding: 48px 0 48px 0;
text-align: left;
}

</style>