<template>
    <div id="formulario">
        <h1>¿Qué personaje es?</h1>
        <p>Escríbelo aquí:</p>
        <input type="text" v-model="nombre">
        <h2 id="contenido"> {{ nombre }}</h2>
        <button v-on:click="cambiarNombre">¡Descubrelo!</button>
        <hr>
    </div>

</template>

<!--Javascript de esta pagina-->
<script>

export default{

    name:'two-way-component',
    data : function(){
        return{
            nombre: ''
        }
    },
    methods:{
        cambiarNombre: function(){
            this.nombre = 'Gandalf';
        }
    },
}
</script>

